import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import style from "./summary.module.css"

const SummaryScreens = () => {
  const data = useStaticQuery(graphql`
  query {
    image1: file(relativePath: { eq: "screenshots/containerframed-1.png" }) {
      childImageSharp {
        fluid(maxWidth: 200) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    image2: file(relativePath: { eq: "screenshots/itemframed-1.png" }) {
      childImageSharp {
        fluid(maxWidth: 200) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
  `)
  
  return (
  <div className={style.summaryscreens}>
    <div className={style.image}>
      <Img fluid={data.image1.childImageSharp.fluid} />
    </div>
    <div className={style.image}>
      <Img fluid={data.image2.childImageSharp.fluid} />
    </div>
  </div>
  )
}

export default SummaryScreens
